@import url('https://fonts.googleapis.com/css2?family=Ubuntu:wght@300;400&display=swap');

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: 'Ubuntu', sans-serif;
}
body {
  font-family: 'Ubuntu', sans-serif;
}

span nav-link {
  font-size: 20px;
  margin-right: 30px;
}

.user {
  height: 35px;
  width: 35px;
  border-radius: 50%;
  background-color: purple;
  color: wheat;
  text-align: center;
}
.user h4 {
  margin-top: 2px;
  margin-left: 2px;
}

.navbar-toggler-icon {
  background-image: url("./asset/logo.png") !important;
}
i {
  color: black !important;
  font-weight: bold;
  cursor: pointer;
  font-size: 20px;
}

.addbook {
  width: 50%;
  margin: auto;
}
.active {
  font-weight: bold;
}

.logout {
  font-size: 24px;
  background-color: transparent;
  margin: 0;
  padding: 0;
}

.signup
{
  height: 100vh;
  display: flex;
  align-items: center;
  justify-content: center;
}
.container {
    width: max-content;
    border-radius: 5px 5px;
    box-shadow: rgba(0, 0, 0, 0.1) 0px 4px 6px -1px, rgba(0, 0, 0, 0.06) 0px 2px 4px -1px;
    display: flex;
    justify-content: center;
    align-items: center;
}

.signup-content {
  display: flex;
  justify-content: space-between;
  flex-direction: row;
  margin: 20px 0;
}
.signup-image {
  display: flex;
  flex-direction: column;
  align-items: center;
}
.signup-form {

  display: inline;
  justify-content: center;
  align-items: center;
}

.signup-form h2
{
  font-weight: bold; 
  margin-left: 20px;
}


.form-group {
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 10px;
}
.form-group input {
  width: 300px;
  outline: none;
  border: none;
  border-radius: 0;
  border-bottom: 1px solid black;
}

.form-group i
{
  
  padding: 10px;
  
}
 input:focus
{
  outline: none !important;
}

.sbutton
{
  margin-left: 40px;
}

@media screen and (max-width: 768px) {
  .addbook {
    width: 100%;
  }
  .signup-content {
    display: flex;
    justify-content: space-between;
    flex-direction: column;
  }
}
@media screen and (max-width: 480px) {
  .addbook {
    width: 100%;
    .signup-content {
      display: flex;
      justify-content: space-between;
      flex-direction: column;
    }
  }
}
