.top-books
{
    width: 100%;
}
.topbook-card
{
    height: 400px;
    width: 400px;
    box-shadow: rgba(99, 99, 99, 0.2) 0px 2px 8px 0px;
    
     margin:20px !important;
}
.topbook-card img
{
    height: 200px;
    object-fit: fill;
}
@media(max-width:900px)
{
    .topbook-card
    {
        height: fit-content;
    }
   
}@media(max-width:768px)
{
    .topbook-card
    {
        height: fit-content;
    }
    .col{
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;
        margin-left: 5px;
    }
}
@media(max-width:480px)
{
    .topbook-card
    {
        height: fit-content;
        width: 300px;
    }
    .col{
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;
        margin-left: 5px;
    }
}